@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.projects {
  -webkit-animation: .8s ease-out 0s 1 fadeIn;
          animation: .8s ease-out 0s 1 fadeIn;
}



