.contact-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -webkit-animation: .4s ease-out 0s 1 slideInFromBottom;
          animation: .4s ease-out 0s 1 slideInFromBottom;
}
.form-row {
  display: -ms-flexbox;
  display: flex;
}
.form-row .form-item:nth-child(2) {
  margin-left: 40px;
}
.form-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-top: 40px;
}
.form-label {
  margin: 10px 0;
  font-weight: bold;
  font-size: 1.2em;
}
.required {
  color: #D03B2B;
}
.form-field {
  height: 50px;
  font-size: 1.2em;
  padding: 0 15px;
  background-color: #fafafa;
  border: 2px solid #e3e3e3;
  border-radius: 5px;
  -webkit-transition: .3s ease;
  -o-transition: .3s ease;
  transition: .3s ease;
}
.form-field:hover {
  background-color: #fff;
  border: 2px solid #666;
  -webkit-transition: .3s ease;
  -o-transition: .3s ease;
  transition: .3s ease;
}
.form-field:focus {
  background-color: #fff;
  border: 2px solid #666;
  -webkit-transition: .3s ease;
  -o-transition: .3s ease;
  transition: .3s ease;
}
.text-field {
  padding-top: 15px;
  height: 200px;
}
.submit-form {
  margin: 40px auto 0 auto;
}
@media (max-width: 767px) {
  .form-row {
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .form-row .form-item:nth-child(2) {
    margin-left: 0px;
  }
  .form-item {
    margin-top: 20px;
  }
}



