@-webkit-keyframes slideInFromTop {
  0% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes slideInFromTop {
  0% {
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@-webkit-keyframes grow {
  0% {
    width: 0%;
  }
  100% {
    width: 50%;
  }
}
@keyframes grow {
  0% {
    width: 0%;
  }
  100% {
    width: 50%;
  }
}
@-webkit-keyframes expandMobileMenu {
  0% {
    height: 0px;
  }
  100% {
    height: 225px;
  }
}
@keyframes expandMobileMenu {
  0% {
    height: 0px;
  }
  100% {
    height: 225px;
  }
}
@-webkit-keyframes hideMobileMenu {
  0% {
    height: 225px;
  }
  100% {
    height: 0px;
  }
}
@keyframes hideMobileMenu {
  0% {
    height: 225px;
  }
  100% {
    height: 0px;
  }
}
.navbar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100px;
  /* border-bottom: solid 2px #000; */
  -webkit-animation: .4s ease-out 0s 1 slideInFromTop;
          animation: .4s ease-out 0s 1 slideInFromTop;
  font-family: 'Press Start 2P', cursive;
  background-color: #f0f0f0;
  padding: 0 50px;
}
.navbar-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
}
.mobile-menu-hidden {
  height: 0px;
  overflow: hidden;
  -webkit-animation: .4s ease 0s 1 hideMobileMenu;
          animation: .4s ease 0s 1 hideMobileMenu;
  display: none;
}
.mobile-menu-display {
  overflow: hidden;
  -webkit-animation: .4s ease 0s 1 expandMobileMenu;
          animation: .4s ease 0s 1 expandMobileMenu;
}
.nav-logo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 1.6em;
  font-weight: bold;
  height: 70px;
  color: #FF887B;
  width: 70px;
  text-decoration: none;
  border: solid 5px #b9b9b9;
  border-radius: 17px;
  background-color: #f2f2f2;
  -webkit-transition: .3s ease;
  -o-transition: .3s ease;
  transition: .3s ease;
}
.nav-logo:hover {
  color: #E96153;
  background-color: #ebebeb;
  -webkit-transition: .3s ease;
  -o-transition: .3s ease;
  transition: .3s ease;
}
.nav-logo-text {

}
.right-nav-items {
  display: -ms-flexbox;
  display: flex;
  list-style: none;
}
.nav-item {
  margin-left: 50px;
  text-decoration: none;
  color: #b9b9b9;
  font-size: 1.1em;
  -webkit-transition: .15s ease;
  -o-transition: .15s ease;
  transition: .15s ease;
}
.mobile-items {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}
.mobile-item {
  margin-bottom: 25px;
  text-decoration: none;
  color: #b9b9b9;
  font-size: 1.25em;
  -webkit-transition: .15s ease;
  -o-transition: .15s ease;
  transition: .15s ease;
}
.nav-item:hover {
  color: #FF887B;
  -webkit-transition: .15s ease;
  -o-transition: .15s ease;
  transition: .15s ease;
}
.active {
  color: #FF887B;
}
.active::after {
  -webkit-animation: .4s ease-out 0s 1 grow;
          animation: .4s ease-out 0s 1 grow;
  content: ' ';
  display: block;
  height: 4px;
  width: 50%;
  background-color: #FF887B;
  position: relative;
  top: 10px;
  /* border-radius: 25%; */
  margin: 0 auto;
}
.hamburger-container {
  display: none;
}
.bar1, .bar2, .bar3 {
  width: 30px;
  height: 3px;
  background-color: #b9b9b9;
  margin: 6px 0;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}
.change .bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px);
  -ms-transform: rotate(-45deg) translate(-6px, 4px);
      transform: rotate(-45deg) translate(-6px, 4px);
}
.change .bar2 {
  opacity: 0;
}
.change .bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px);
  -ms-transform: rotate(45deg) translate(-8px, -8px);
      transform: rotate(45deg) translate(-8px, -8px);
}
@media (max-width: 1023px) {
  .navbar {
    height: auto;
    padding: 0 25px;
    /* border-bottom: solid 1px #000; */
  }
  .nav-logo {
    font-size: 1em;
    height: 50px;
    width: 50px;
    border: none;
    margin: 10px 0;
    border: solid 3px #b9b9b9;
  }
  .right-nav-items {
    display: none;
  }
  .hamburger-container {
    display: inline-block;
    cursor: pointer;
  }
  .active::after {
    -webkit-animation: none;
            animation: none;
    content: none;
    display: none;
    height: none;
    width: none;
    background-color: none;
    position: none;
    top: none;
    border-radius: none;
    margin: none;
  }
}



